
/* FormDemo.css */

.form .card {
    min-width: 450px;
}
.form .card form {
    margin-top: 2rem;
}
.form .card .field {
    margin-bottom: 1.5rem;
}
@media screen and (max-width: 2024px) {
    .form .card {
        margin-top: 15rem;
    }
    .form .card {
        width: 30%;
    }
}
@media screen and (max-width: 960px) {
    .form .card {
        margin-top: 10rem;
    }
    .form .card {
        width: 80%;
    }
}
@media screen and (max-width: 640px) {
    .form .card {
        margin-top: 5rem;
    }
    .form .card {
        width: 100%;
        min-width: 0;
    }
}
             