table {
  border-collapse: collapse;
  width: 100%;
}

tr, td {
  border: 1px solid #ddd;
  padding: 5px;
  text-align: center;
}

th {
  background-color: #f2f2f2;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

.td1, .td2{
  font-size: 15px;
  font-weight: 700;
}

@media screen and (max-width: 640px) {
  .table-1{
    width: 100%;
  }

  .table-2{
    width: 100%;
  }

  .td1{
    width: 39%;
    text-align: left;
  }
  
  .td2{
    text-align: left;
  }
  .col-12{
    padding-bottom: 0px;
  }
}

